<template lang="html">

  <div class="proyectos">

    <section class="vh intro">
      <div class="bkg-pieza waypoints-instant -responsiveIntro" data="fadeInUp">
        <a href="#" v-scroll-to="'.cat-1'" class="scrolldown"></a>
        <div class="col-50 flex column nowrap justify-center align-center animated fadeInLeft -introText">
          <div class="container-text">
            <h2 class="title title-52" v-html="$t('proyectos_IDI.titulo')"></h2>
            <p v-html="$t('proyectos_IDI.entradilla')"></p>
          </div>
        </div>
      </div>
    </section>


    <section class="proyecto-cat flex wrap justify-end align-stretch ais">
      <div class="cat-1 img-cat" data-value="bkg-cat1"></div>
      <div class="cat-1 desc-cat flex column justify-center">
        <h2 class="title title-36" v-html="$t('proyectos_IDI.hidrogen_station.title')"></h2>
        <p  v-html="$t('proyectos_IDI.hidrogen_station.description')"></p>
        <p v-html="$t('proyectos_IDI.hidrogen_station.description_2')"></p>
      </div>
    </section>

    <section class="proyecto-cat flex wrap justify-end align-stretch">
      <div class="cat-2 desc-cat flex column justify-center">
        <h2 class="title title-36"  v-html="$t('proyectos_IDI.qhyxote_home.title')"></h2>
        <p v-html="$t('proyectos_IDI.qhyxote_home.description')"></p>
      </div>
      <div class="cat-2 img-cat" data-value="bkg-cat2"></div>
    </section>

    <section class="proyecto-cat flex wrap justify-end align-stretch">
      <div class="cat-3 img-cat" data-value="bkg-cat3"></div>
      <div class="cat-3 desc-cat flex column justify-center with-button">
        <h2 class="title title-36" v-html="$t('proyectos_IDI.qhyxote_car.title')"></h2>
        <p v-html="$t('proyectos_IDI.qhyxote_car.description')"></p>
        <button class="show-section" :show="proyecto_3" @click="proyecto_3 = !proyecto_3"></button>
      </div>
    </section>

    <section class="proyecto-texto" :hide="!proyecto_3">

      <div class="container">
        <p v-html="$t('proyectos_IDI.qhyxote_car.desplegable.texto_1')"></p>
        <p v-html="$t('proyectos_IDI.qhyxote_car.desplegable.texto_2')"></p>
        <p v-html="$t('proyectos_IDI.qhyxote_car.desplegable.texto_3')"></p>
        <p v-html="$t('proyectos_IDI.qhyxote_car.desplegable.texto_4')"></p>
        <p v-html="$t('proyectos_IDI.qhyxote_car.desplegable.texto_5')"></p>
      </div>

    </section>

    <section class="proyecto-cat flex wrap justify-end align-stretch">
      <div class="cat-4 desc-cat flex column justify-center">
        <h2 class="title title-36"  v-html="$t('proyectos_IDI.qhyxote_scooter.title')"></h2>
        <p v-html="$t('proyectos_IDI.qhyxote_scooter.description')" ></p>
      </div>
      <div class="cat-4 img-cat flex wrap justify-end align-stretch" data-value="bkg-cat4"></div>
    </section>

    <section class="proyecto-cat flex wrap justify-end align-stretch">
      <div class="cat-5 img-cat flex wrap justify-end align-stretch" data-value="bkg-cat5"></div>
      <div class="cat-5 desc-cat flex column justify-center">
        <h2 class="title title-36" v-html="$t('proyectos_IDI.unidad_ponencia.title')"></h2>
        <p v-html="$t('proyectos_IDI.unidad_ponencia.description_1')"></p>
        <p v-html="$t('proyectos_IDI.unidad_ponencia.description_2')"></p>
      </div>
    </section>

    <section class="proyecto-cat flex wrap justify-end align-stretch">
      <div class="cat-6 desc-cat flex column justify-center with-button">
        <h2 class="title title-36"  v-html="$t('proyectos_IDI.cogeneracion.title')"></h2>
        <p v-html="$t('proyectos_IDI.cogeneracion.description')"></p>
        <button class="show-section" :show="proyecto_6" @click="proyecto_6 = !proyecto_6"></button>
      </div>
      <div class="cat-6 img-cat flex wrap justify-end align-stretch" data-value="bkg-cat6"></div>
    </section>

    <section class="proyecto-texto" :hide="!proyecto_6">
      <div class="container">
       <p v-html="$t('proyectos_IDI.cogeneracion.desplegable.texto_1')"></p>
       <p v-html="$t('proyectos_IDI.cogeneracion.desplegable.texto_2')"></p>
      </div>
    </section>

    <otras-secciones :data="['productos','tecnologia']"></otras-secciones>

  </div>

</template>

<script lang="js">
  
  import Otras_Secciones from '@/components/parts/acceso-a-secciones';

  export default  {
    name: 'proyectos',
    props: [],
    mounted() {

    },
    data() {
      return {
        proyecto_3 : false,
        proyecto_6 : false
      }
    },
    methods: {

    },
    components: {
      'otras-secciones' : Otras_Secciones
    }
  }
</script>

<style scoped lang="scss">

.proyecto-cat .desc-cat{
  position: relative;
}

.show-section {
  position: absolute;
    left: 0;
    bottom: 0;
    height: 100px;
    font-size: 40px;
    font-weight: 300;
    color: #fff;
    background: transparent url(/static/img/more-gray.svg) center/auto 40px no-repeat;
    border: none;
    width: 100%;
    outline: none;
    cursor: pointer;
    -webkit-transition: .5s;
    transition: .5s;
    background-position-x: 90%;
}

.show-section[show="true"]{
  background: transparent url(/static/img/less-gray.svg) 90%/ auto 40px no-repeat;
}

.proyecto-texto{
  padding: 50px;
  background: url(/static/img/bkg-metal.jpg) center/cover no-repeat;
  -webkit-transition: height .3s linear, padding-top .3s linear, padding-bottom .3s linear, border-top-width .3s linear, border-top-width .3s linear;
  transition: height .3s linear, padding-top .3s linear, padding-bottom .3s linear, border-top-width .3s linear, border-top-width .3s linear;
}

.proyecto-texto[hide="true"]{
  overflow: hidden;
  padding-top: 0;
  padding-bottom: 0;
  height: 0;
  border-width: 0 1px;
}

.proyecto-texto p{
  color: #646464;
  line-height: 25px;
  font-size: 16px;
  // font-family: 'Conv_Ahmet Altun - HalisGR-Book';
}

@media screen and (max-width: 1200px){
  
  .proyecto-cat .img-cat{
    width: 50%;
  }

  .proyecto-cat .desc-cat{
    width: 50%;
  }
}

@media screen and (max-width: 900px){
  
  .proyecto-cat{
    min-height: 0;
  }

  .proyecto-cat .img-cat{
    width: 100%;
    min-height: 33.33333vh;
    order: -1;
  }

  .proyecto-cat .desc-cat{
    width: 100%;
    min-height: 0;
  }

  .proyecto-cat .desc-cat.with-button{
    padding-bottom: 80px;
  }
  
  .proyecto-texto{
    padding: 20px 0;
  }

  .proyecto-cat .title{
    margin-bottom: 15px;
  }

}

</style>



