  import Otras_Secciones from '@/components/parts/acceso-a-secciones';

  export default  {
    name: 'trabaja-con-nosotros',
    props: [],
    mounted() {

    },
    data() {
      return {
        
      }
    },
    methods: {

    },
    components: {
      'formulario' : formulario,
      'otras-secciones' : Otras_Secciones,
    }
  }
</script>

<style scoped>
.intro-xs .container-text{
  max-width: 570px;
  padding: 0;
  margin-right: 0;
}

.intro-xs h2{
  position: relative;
  max-width: 600px;
}
</style>



